<template>
    <div class="line-chart">
         <chart :options="options" :autoresize="true"/>
    </div>
</template>
<script>
import ECharts from 'vue-echarts';
import 'echarts/lib/chart/line';
import 'echarts/lib/chart/pie';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';
export default {
    name:'lineChart',
    components:{
        chart:ECharts
    },
    data() {
        return {
            options: {
                
                tooltip: {
                    formatter: '{c}'
                },
                xAxis: {
                    type: 'category',
                    data: ['$10', '$20', '$30', '$40', '$50']
                },
                yAxis: {
                    type: 'value'
                },
                color: '#37acc6',
                series: [
                    {
                        data: [10, 30, 27, 50, 45, 70,90],
                        type: 'line',
                        smooth: true
                    }
                ]
            }
        }
    }
}
</script>
<style lang="less">

</style>



