<template>
    <div class="nav-module">
        <ul class="nav-ul">
            <li class="nav-li" :class="index == id ? 'nav-active-li':''" v-for="(item,index) in data" :key="index">{{item}}</li>
        </ul>
    </div>
</template>
<script>
export default {
    name:'headNav',
    data() {
        return {
            data:['Dashboard','Services','History','Actions'],
            id:0
        }
    }
}
</script>
<style lang="less">
.nav-module {
    width: 100%;
    padding-right: 20px;
    box-sizing: border-box;
    .nav-ul {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        .nav-li {
            color: #727582;
            font-size: 16px;
            list-style: none;
            padding: 0 20px;
        }
        .nav-active-li {
            color: #223047;
        }
    }
}
</style>



