<template>
    <div class="line-chart">
         <chart :options="options" :autoresize="true"/>
    </div>
</template>
<script>
import ECharts from 'vue-echarts';
import 'echarts/lib/chart/pie';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/legend';
export default {
    name:'pieChart',
    components:{
        chart:ECharts
    },
    data() {
        return {
            options: {
                tooltip: {
                    trigger: 'item',
                    formatter: '{b}: ({d}%)'
                },
                legend: {
                    orient: 'vertical',
                    left: 10,
                    data: ['', '', '', '']
                },
                series: [
                    {
                        name: '',
                        type: 'pie',
                        radius: ['50%', '70%'],
                        avoidLabelOverlap: false,
                        label: {
                            normal: {
                                show: false,
                                position: 'center'
                            },
                            emphasis: {
                                show: true,
                                textStyle: {
                                    fontSize: '30',
                                    fontWeight: 'bold'
                                }
                            }
                        },
                        labelLine: {
                            normal: {
                                show: false
                            }
                        },
                        data: [
                            { value: 335, name: '$5,399',label:{normal:{color:'#000'}},itemStyle:{normal:{color:'#ccc'}} },
                            { value: 310, name: '$5,699',label:{normal:{color:'#000'}},itemStyle:{normal:{color:'#333'}} },
                            { value: 234, name: '$5,599',label:{normal:{color:'#000'}},itemStyle:{normal:{color:'#1b919e'}} },
                            { value: 135, name: '$5,499',label:{normal:{color:'#000'}},itemStyle:{normal:{color:'#f00'}} }
                        ]
                    }
                ]
            }
        }
    }
}
</script>
<style lang="less">

</style>


