<template>
    <div class="home-module">
        <div class="content">
            <div class="main">
                <div class="header">
                    <div class="logo-box">
                        <img class="logo" src="@/assets/demo-logo.png" alt="logo" title="logo">
                    </div>
                    <div class="nav">
                        <head-nav></head-nav>
                    </div>
                    <div class="search-box">
                        <input class="search-value" type="text" name="sreach" id="sreach">
                        <img class="search-btn" src="@/assets/search.png" alt="search" title="">
                    </div>
                </div>
                <div class="cards-box">
