                                Wacom LCD
                            </div>
                            <div class="number">
                                -$3000
                            </div>
                        </div>
                    </div>
                    <div class="trans-btn">
                        <button class="button">
                            <span>Transfer Money</span>
                            <img src="@/assets/arrow_right.png" alt="arrow">
                        </button>
                    </div>
                </div>
                <div class="aside-message">
                    <img src="@/assets/message.png" alt="message">
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import lineChart from '@/components/lineChart'
import pieChart from '@/components/pieChart'
import headNav from '@/components/nav'
import cards from '@/components/cardsList'
export default {
    name: 'Home',
    components: {
        lineChart,
        pieChart,
        headNav,
        cards
    },
    data() {
        return {
            data:[
                {
                    imgUrl:require('@/assets/a_01.png'),
                    name:'Transactions'
                },
                {
                    imgUrl:require('@/assets/a_02.png'),
                    name:'Utility'
                },
                {
                    imgUrl:require('@/assets/a_03.png'),
                    name:'Loans'
                },
                {
                    imgUrl:require('@/assets/a_04.png'),
                    name:'Deposits'
                },
                {
                    imgUrl:require('@/assets/a_05.png'),
                    name:'Fast transfer'
                },
                {
                    imgUrl:require('@/assets/a_06.png'),
                    name:'Exchange'
                },
            ]
        };
    }
};
</script>
<style lang="less" >
.home-module {
    width: 100%;
    height: 100%;
    background: #fdebea;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    .content {
        width: 100%;
        height: 100%;
        background: #fff;
        border: solid 12px #fddada;
        border-radius: 50px;
        box-shadow: 50px 40px 30px #f5dada;
        box-sizing: border-box;
        overflow: hidden;
        display: flex;
        justify-content: space-between;
        margin: 5% 10%;
        min-width: 990px;
        overflow: auto;
        .main {
            width: 74%;
            padding: 40px 60px;
            box-sizing: border-box;
            .header {
                display: flex;
                justify-content: space-between;
                align-items: center;
                .logo-box {
                    width: 50px;
                    .logo {
                        width: 100%;
                    }
                }
                .nav {
                    flex:1;
                }
                .search-box {
                    width: 270px;
                    height: 40px;
                    background: #f8f8f8;
                    border:none;
                    border-radius: 40px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    overflow: hidden;
                    .search-value {
                        width: 100%;
                        height: 38px;
                        border: none;
                        outline: none;
                        background: #f8f8f8;
                        margin-left: 16px;
                    }
                    .search-btn {
                        width: 18px;
                        margin: 0 10px;
                    }
                }
            }
            .cards-box {
                .title {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    margin: 60px auto 30px;
                }
            }
            .services-box {
                display: flex;
                margin-top: 45px;
                .service-left {
                    width: 255px;
                    margin-right: 60px;
                    .title {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                    }
                    .service-list {
                        display: flex;
                        justify-content: space-between;
                        flex-wrap: wrap;
                        margin-top: 30px;
                        .service-item {
                            width: 47%;
                            max-width: 120px;
                            min-height: 100px;
                            max-height: 120px;
                            box-sizing: border-box;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            flex-direction: column;
                            border: none;
                            border-radius: 20px;
                            box-shadow: 0px 0px 20px #ececec;
                            margin-bottom: 20px;
                            img {
                                width: 30%;
                                display: block;
                            }
                            span {
                                width: 100%;
                                display: block;
                                text-align: center;
                                text-overflow: ellipsis;
                                overflow: hidden;
                                white-space: nowrap;
                                margin-top: 5px;
                                font-weight: bold;
                            }
                        }
                    }
                }
                .service-right {
                    width: 68%;
                    .current {
                        width: 100%;
                        .title {
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                        }
                        .current-data {
                            margin-top: 30px;
                            .data-item {
                                display: flex;
                                justify-content: space-between;
                                align-items: center;
                                margin-bottom: 20px;
                                .name {
                                    font-size: 16px;
                                    color: #ccc;
                                }
                                .line {
                                    width: 80%;
                                    margin: 0 20px;
                                    border-bottom: dashed 1px #ccc;
                                }
                                .number {
                                    font-size: 16px;
                                    color: #000;
                                    font-weight: bold;
                                }
                            }
                        }
                    }
                    .savings {
                        margin-top: 30px;
                        .title {
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                        }
                        .saving-data {
                            display: flex;
                            justify-content: space-between;
                            box-sizing: border-box;
                            .data-echart {
                                width: 60%;
                                height: 300px;
                                margin-right: 60px;
                                .line-chart{
                                    width: 100%;
                                    height: 100%;
                                    margin-top: -30px;
                                }
                                .echarts {
                                    width: 100%;
                                    height: 100%;
                                }
                            }
                            .data-main {
                                width: 175px;
                                .data-li {
                                    display: flex;
                                    justify-content: space-between;
                                    align-items: center;
                                    margin-top: 24px;
                                    .label {
                                        font-size: 16px;
                                        color: #ccc;
                                    }
                                    .value {
                                        font-size: 16px;
                                        color: #000;
                                        font-weight: bold;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        .aside {
            width: 26%;
            border: none;
            border-radius: 50px;
            box-shadow: -20px 0 30px #f6f6f6;
            box-sizing: border-box;
            padding: 38px 28px;
            position: relative;
            background: #fff;
            .aside-header {
                display: flex;
                justify-content: space-between;
                align-items: center;
                .confirm {
                    position: relative;
                    img {
                        width: 42px;
                    }
                    .circle {
                        width: 8px;
                        height: 8px;
                        display: block;
                        position: absolute;
                        border: none;
                        border-radius: 50%;
                        background: #f50;
                        top: 6px;
                        right: 9px;
                    }
                }
                .user{
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    .userImg {
                        width: 60px;
                        height: 60px;
                        border: none;
                        border-radius: 50%;
                        overflow: hidden;
                        margin-left: 10px;
                        img {
                            width: 100%;
                            height: 100%;
                        }
                    }
                    .username {
                        font-size: 16px;
                        color: #333;
                        font-weight: bold;
                    }
                }
            }
            .aside-echart {
                width: 100%;
                height: 300px;
                .line-chart {
                    width: 100%;
                    height: 100%;
                }
                .echarts {
                    width: 100%;
                    height: 100%;
                }
            }
            .aside-real-data {
                width: 100%;
                margin-top: 30px;
                .title {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    .label {
                        color: #b8b9c0;
                        font-size: 16px;
                    }
                    .more {
                        color: #b8b9c0;
                        font-size: 16px;
                        display: flex;
                        justify-content: flex-end;
                        align-items: center;
                        img {
                            width: 20px;
                            margin-left: 5px;
                        }
                    }
                }
                .data-list {
                    .list-item {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        margin: 20px auto;
                        .icon {
                            width: 60px;
                            height: 60px;
                            background: #fefefe;
                            border: none;
                            border-radius: 8px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            box-shadow: 1px 1px 60px #e0e0e0;
                            img {
                                width: 40px;
                            }
                        }
                        .name {
                            font-size: 16px;
                            color: #333;
                            font-weight: bold;
                        }
                        .number {
                            color: #f50;
                            font-weight: bold;
                            font-size: 16px;
                        }
                        .add {
                            color: #37acc6;
                        }
                    }
                }
                .trans-btn {
                    width: 100%;
                    padding: 0 10px;
                    margin-top: 60px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    box-sizing: border-box;
                    .button {
                        width: 100%;
                        height: 54px;
                        padding: 0 20px;
                        color: #fff;
                        font-size: 16px;
                        font-weight: bold;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        background: #ff4d56;
                        border: none;
                        outline: none;
                        border-radius: 40px;
                        box-sizing: border-box;
                    }
                }
            }
            .aside-message {
                width: 55px;
                height: 55px;
                border: none;
                border-radius: 50%;
                background: #0e809a;
                display: flex;
                justify-content: center;
                align-items: center;
                position: absolute;
                right: 28px;
                bottom: 38px;
                img {
                    width: 50%;
                    margin-top: 5px;
                    margin-left: 3px;
                }
            }
        }
    }
}
</style>



