<template>
    <div class="card-module">
        <ul class="cards-list">
            <li class="cards-item" v-for="(item,index) in cardsList" :key="index">
                <div class="balance-box">
                    <span class="label">Available Balance</span>
                    <span class="value">{{item.balance}}</span>
                </div>
                <div class="card-number">
                    {{item.cardNum}}
                </div>
                <div class="card-belong">
                    <div class="name">{{item.bankName}}</div>
                    <div class="circle">
                        <span class="circle-item"></span>
                        <span class="circle-item"></span>
                    </div>
                </div>
            </li>
            <li class="cards-item card-add-btn">
                <div class="add-btn">
                    <div class="add-icon">
                        <img src="@/assets/jia.png" alt="" title="">
                    </div>
                    <p>Add New Card</p>
                </div>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name:'CardsList',
    data() {
        return {
            cardsList:[
                {
                    balance:'$3,999',
                    cardNum:'**** **** **** **34',
                    bankName:'Monzo'
                },
                {
                    balance:'$12,999',
                    cardNum:'**** **** **** **34',
                    bankName:'Monzo'
                }
            ]
        }
    }
}
</script>
<style lang="less">
.card-module {
    width: 100%;
    .cards-list {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        .cards-item {
            width: 30%;
            max-width: 300px;
            min-width: 255px;
            height: 175px;
            overflow: hidden;
            border: none;
            border-radius: 20px;
            background: #ff4d56;
            box-sizing: border-box;
            padding: 20px 40px;
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            margin-right: 5%;
            margin-bottom: 20px;
            &:nth-child(2){
                background: #0e809a;
            }
            .balance-box {
                .label {
                    font-size: 14px;
                    color: #727582;
                    display: block;
                }
                .value {
                    font-size: 18px;
                    color: #fff;
                    font-weight: bold;
                    display: block;
                    margin-top: 10px;
                }
            }
            .card-number {
                color: #333;
                font-size: 18px;
            }
            .card-belong {
                display: flex;
                justify-content: space-between;
                align-items: flex-end;
                .name {
                    color: #fff;
                    font-weight: bold;
                    font-size: 18px;
                }
                .circle {
                    display: flex;
                    align-items: center;
                    margin-right: -20px;
                    .circle-item {
                        display: block;
                        width: 35px;
                        height: 35px;
                        border: none;
                        border-radius: 50%;
                        &:nth-child(1) {
                            background: #ff3a4f;
                            margin-right: -13px;
                        }
                        &:nth-child(2){
                            background: #ffc24b;
                        }
                    }
                }
            }
        }
        .card-add-btn {
            background: #f8f8f8;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-right: 0;
            .add-btn {
                display: flex;
                justify-content: center;
                align-items: center;
                flex-wrap: wrap;
                padding-top: 20px;
                .add-icon {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border: dashed 1px #ccc;
                    border-radius: 50%;
                    padding: 7px;
                    img {
                        width: 20px;
                    }
                }
                p {
                    width: 100%;
                    text-align: center;
                    margin-top: 10px;
                }
            }
        }
    }
}
</style>



